#isso-thread {
	font-size: 1.8rem;
	font-family: Georgia, serif;
}

#isso-thread h4 {
	font-size: 3.5rem;
	margin-top: 0px;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#isso-thread .auth-section input {
	padding-top: 4.8px;
	padding-bottom: 4.8px;
}

#isso-thread .auth-section input[type=submit] {
	padding-left: 10px;
	padding-right: 10px;
	width: auto;
}

#isso-thread .isso-postbox {
	margin-bottom: 0px;
}

#isso-thread .isso-comment {
	margin-top: 0px;
}



.post-wedding .post-date,
.post-comments {
    display: inline-block;
    margin-left: 8px;
    padding-left: 12px;
    border-left: #d5dbde 1px solid; !important
    font-size: 1.3rem;
    white-space: nowrap;
}

.page-wedding .page {
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: #EBF2F6 1px solid;
}

.post-wedding {
    position: relative;
    width: 80%;
    max-width: 710px;
    margin: 2rem auto;
    padding-bottom: 2rem;
    border-bottom: #EBF2F6 1px solid;
    word-wrap: break-word;
}

.page-wedding .page:after,
.post-wedding:after {
display: block;
content: "";
width: 7px;
height: 7px;
border: #E7EEF2 1px solid;
position: absolute;
bottom: -5px;
left: 50%;
margin-left: -5px;
background: #FFF;
border-radius: 100%;
box-shadow: #FFF 0 0 0 5px;
}

.post-wedding a {
text-decoration: none;
}

.post-wedding .post-excerpt p {
font-size: 1em;
}

@media only screen and (min-width: 600px) {
	#isso-thread {
	  padding: 0px 7vw !important;
	}

	#isso-thread .auth-section .input-wrapper {
		margin-right: 5px;
	}
}

@media only screen and (max-width: 500px) {
.post-wedding {
margin-left: 16px;
margin-right: 16px;
width: auto;
}
.post-wedding .post-date {
display: none;
}
.post-wedding .post-excerpt p {
font-size: 0.9em;
}
}

.gh-navbar-item {
  padding: 0;
}

.gh-nav-logo img {
  display: block;
  width: auto;
  height: 21px !important;
}

.gh-navbar-left a,
.nav li a {
	color: #738a94;
}

.gh-mobilemenu ul.nav {
	display: block;
}

.gh-mobilenavbar .gh-nav-logo {
		right: 13px;
    margin-right: 0px;
}

.slideshow {
        position: relative;
        font-family: Verdana,sans-serif;
        font-size: 15px;
        line-height: 1.5;
        display: block;
	margin: 0 0 1.5em;
}

.slideshow:before, .slideshow:after {
        box-sizing: inherit;
}

.slideshow img {
        display:none;
        width:100%
}

.slideshow .controls {
        width: 100%;
        text-align: center!important;
        padding: 0.01em 16px !important;
        margin-top: 16px!important;
        margin-bottom: 16px!important;
        font-size: 18px!important;
        color: #fff!important;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%,0%);
        -ms-transform: translate(-50%,0%);
        display: block;
        box-sizing: inherit;
}

.slideshow .controls:after, .slideshow .controls:before {
        content: "";
        display: table;
        clear: both;
        box-sizing: inherit;
}

.slideshow .controls div {
        cursor:pointer
}

.slideshow .demo {
        height:13px;
        width:13px;
        color: #fff;
        margin: 0px 2px !important;
        display: inline-block;
        padding: 0;
        cursor:pointer;
        border: 1px solid #ccc!important;
        border-radius: 50%;
        text-align: center;
        font-family: Verdana,sans-serif;
        line-height: 1.5;
        box-sizing: inherit;
}

.slideshow .white, .slideshow .demo:hover {
        color: #000!important;
        background-color: #fff!important;
}
